
import headerBack from "../../components/header-back";
import Confirm from 'vux/src/components/confirm/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import VueDB from "../../util/vue-db/vue-db-long.js";
var intervalTime = null;
var DB = new VueDB();

export default {
name: "register",
data() {
return {
holdPhone:this.$t('register.holdPhone'),
holdNewPassword:this.$t('register.holdNewPassword'),
inputVerificationCode:this.$t('register.inputVerificationCode'),

isMsgShow: false,
countDown: 120,
sjtLogo: "../../../static/images/mine/sjtLogin.jpg",
sjtLogo2: "../../../static/images/mine/sjtLogo1.jpg",
iconType: '',

register: {
telephone: null,
identifyCode:null,
passwords: null
},
isUserInsert: true,
comparePollCodeData:null,
userNo:null, //用户编码
}
},
components: {
XImg,
},
mounted: function() {
this.initData();
},
methods: {
initData() {

},
open(link) {
this.$router.openPage(link);
},
getMsgCode() {
if(this.register.telephone) {

this.isMsgShow = true;
intervalTime = setInterval(() => {
this.countDown--;
if(this.countDown <= 0) {
clearInterval(intervalTime);
this.isMsgShow = false;
this.countDown = 120;
}
}, 1000)
this.findUserByTelephoneInWeb();
} else {
this.$vux.toast.show({
text: "请填写手机号！",
type: "text",
})
}
},

userRegister() {
if(!this.register.telephone && !this.register.passwords && !this.register.identifyCode) {
this.$vux.toast.show({
text: "请填写正确的用户信息！",
type: "cancel",
})
return;
} else {
this.comparePollCode();

}
},


findUserByTelephoneInWeb() {
this.$http.get("/userRegister/findUserByTelephone", {
params: {
telephone: this.register.telephone,
}
}).then((res) => {
if(res.status == 200 && res.data.rspCode == "00000") {
console.log(res.data.data)
console.log("根据手机号码查找用户移动端")
if(!res.data.data) {

this.$vux.toast.show({
text: this.$t("reminder.pleaseLogout"),
type: "text",
})
clearInterval(intervalTime);
this.isMsgShow = false;
this.countDown = 120;
return false;
} else {


console.log("已经存在的用户");
this.createPollCode();
}
}
}).catch((err) => {
console.log(err)
})
},

findUserByTelephoneInWx() {
},


createPollCode() {
this.$http.get("/userRegister/createPollCode", {
params: {
telephone: this.register.telephone
}
}).then((res) => {
console.log("发送短信成功");
if(res.status == 200) {
console.log(res.data)
if(res.data) {
this.$vux.toast.show({
text: "信息已发送,请注意查收！",
type: "text",
})
}
}
}).catch((err) => {
console.log(err)
})

},

comparePollCode() {
this.comparePollCodeData = null;
this.$http.post("/userRegister/comparePollCode", {
telephone: this.register.telephone,
identifyCode: this.register.identifyCode
}).then((res) => {
console.log("比较验证码成功");
console.log(res.data)
if(res.status == 200){
if(DB.getItem("localLang").toString() == "en"){
var ErrorMsg = res.data.usErrorMsg;
}else{
var ErrorMsg = res.data.cnErrorMsg;
}
switch(res.data.rspCode){
case "00000":
this.comparePollCodeData = 1;
break;
default:
this.$vux.toast.show({
text: ErrorMsg,
type: "text",
})
break;
}

console.log(this.comparePollCodeData)
console.log("this.comparePollCodeData")
if(this.comparePollCodeData){

this.modifyUserPassword();
}
}
}).catch((err) => {
console.log(err)
})
},

modifyUserPassword(){
this.$http.get("/userRegister/modifyUserPassword", {
params:{
telephone: this.register.telephone,
userPassword: this.register.passwords,
}
}).then((res) => {
console.log(res)
if(res.status == 200){
if(res.data.rspCode == "00000"){
this.$vux.toast.show({
text: "登录成功！",
type: "text",
})
this.findUserByTelephoneEnd();
setTimeout(()=>{
this.$router.openPage("/closeAccount");
},1000)
}
}
}).catch((err) => {
console.log(err)
})
},


bindOpenidTel() {
},

insertTel() {
},

findUserByTelephoneEnd() {
this.$http.get("/userRegister/findUserByTelephone", {
params: {
telephone: this.register.telephone
}
}).then((res) => {
if(res.status == 200 && res.data.rspCode == "00000") {
console.log("根据手机号码查找用户,确定用户已经存在了，存储userNo,telephone")
console.log(res.data.data)
if(res.data.data) {
var telUserNo = {telephone:res.data.data.telephone,userNo:res.data.data.userNo,weixinOpenid:res.data.data.weixinOpenid}
console.log("telUserNo")
console.log(telUserNo)
DB.setItem("telUserNo",telUserNo);
}
}
}).catch((err) => {
console.log(err)
})
},

}

}



